import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { StoreTransactionRoutingModule } from './store-transaction-routing.module';
import { StoreTransactionComponent } from './store-transaction.component';
import { StoreTransactionAddComponent } from './store-transaction-add/store-transaction-add.component';
import { StoreTransactionEditComponent } from './store-transaction-edit/store-transaction-edit.component';

@NgModule({
  imports: [
    CommonModule,
     SharedModule,
    StoreTransactionRoutingModule,
  ],
  entryComponents:[
    StoreTransactionAddComponent,
    StoreTransactionEditComponent
  ],
declarations: [
  StoreTransactionComponent,
  StoreTransactionAddComponent,
  StoreTransactionEditComponent
],
schemas: [ NO_ERRORS_SCHEMA ]
})
export class StoreTransactionModule { }



