import { Component, Inject, OnInit, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { StoreTransactionService, CurrentUserService, ErrorHandleService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-store-transaction-add',
  templateUrl: './store-transaction-add.component.html',
  styleUrls: ['./store-transaction-add.component.scss']
})
export class StoreTransactionAddComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('sComboBox') sComboBox: jqxComboBoxComponent;
  @ViewChild('iComboBox') iComboBox: jqxComboBoxComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  storeForm: FormGroup;
  apiUrl: string;
  store_data: any;
  storeAdapter: any;
  item_data: any;
  itemAdapter: any;
  storeChanged: boolean = false;
  itemChanged: boolean = false;

  userData: any = {};

  constructor(
    private fb: FormBuilder,
    private stService: StoreTransactionService,
    private ehs: ErrorHandleService,
    @Inject('API_URL') apiUrl: string,
    private cus: CurrentUserService,
  ) {
    this.apiUrl = apiUrl;
    this.userData['token'] = this.cus.getTokenData()['token'];
    this.createForm();
  }

  ngOnInit() {
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'store?limit=all',
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
    this.item_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'item?limit=all&transact=1',
      };
    this.itemAdapter = new jqx.dataAdapter(this.item_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        for (let i = 0; i < tData.length; i++) {
          tData[i]['name'] = tData[i]['name'] + ' - ' + tData[i]['code']
        }
        return tData;
      }
    });
  }
  createForm() {
    this.storeForm = this.fb.group({
      'quantity': [null, Validators.required],
      // 'value': [null],
      'rate': [null, Validators.required],
      // 'total': [null],
      'remarks': [null],

    });
  }

  saveBtn(post, afterSuccess) {
    post['store_id'] = this.sComboBox.val();
    post['item_id'] = this.iComboBox.val();
    this.myValidator.validate(document.getElementById('storeForm'));
    if (post['store_id'] && post['item_id']) {
      this.jqxLoader.open();
      this.stService.store(post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            data['afterSuccess'] = afterSuccess;
            if(afterSuccess == 'save'){
              this.storeForm.reset();
            }
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }
  }
  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      {
        input: '.store_id', message: 'Store name required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.sComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },

      { input: '.quantity', message: 'Quantity required!', action: 'keydown, blur', rule: 'required' },
      {
        input: '.item_id', message: 'Item name required!', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.iComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      // { input: '.remarks', message: 'Remarks required!', action: 'keydown, blur', rule: 'required' },
      // { input: '.value', message: 'Value required!', action: 'keydown, blur', rule: 'required' },
      // { input: '.total', message: 'Total required!', action: 'keydown, blur', rule: 'required' },
      { input: '.rate', message: 'Rate required!', action: 'keydown, blur', rule: 'required' },

    ];

    storeNameChanged() {
      this.storeChanged = this.sComboBox.val()?true:false;
      console.log(this.storeChanged);
    }

    itemNameChanged() {
      this.itemChanged = this.iComboBox.val() ? true:false;
      console.log(this.itemChanged);  
    }

}



